<template>
  <div>
    <md-table-card v-show="!showProj" class="m-project-card">
      <md-toolbar class="md-medium">
        <h1 class="md-title app-toolbar-title"></h1>

        <md-button class="md-icon-button" @click="dealProject($event, 'add')">
          <md-icon>add</md-icon>
        </md-button>
          
        <md-button class="md-icon-button  md-warn" @click="openDiag">
          <md-icon>clear</md-icon>
        </md-button>

        <md-button class="md-icon-button" @click="controlProjectStatus($event, 1)">
          <md-icon>play_arrow</md-icon>
        </md-button>

        <md-button class="md-icon-button md-warn" @click="controlProjectStatus($event, 0)">
          <md-icon>stop_arrow</md-icon>
        </md-button>

        <md-button class="md-icon-button" @click="buttonSearch">
          <md-icon>search</md-icon>
        </md-button>
      </md-toolbar>

      <md-table @select="selectTable" id="mytable">
        <md-table-header>
          <md-table-row>
            <md-table-head class="m-min-width"></md-table-head>
            <md-table-head></md-table-head>
            <md-table-head md-tooltip=""></md-table-head>
            <md-table-head class="m-min-width" md-tooltip="API"></md-table-head>
            <md-table-head class="m-min-width">&nbsp;</md-table-head>
          </md-table-row>
        </md-table-header>

        <md-table-body>
          <md-table-row v-for="(row, rowIndex) in list" :key="rowIndex" :md-item="row" :data-item="row._id" md-auto-select md-selection>
            <md-table-cell>
              <md-icon class="md-primary m-inline" v-if="row.status">lightbulb_outline</md-icon>
              <md-icon class="m-inline" v-else>lightbulb_outline</md-icon>
            </md-table-cell>
            <md-table-cell>
              <span>{{ row.name }}</span>
            </md-table-cell>
            <md-table-cell><span>{{ row.path }}</span></md-table-cell>
            <md-table-cell><span>{{ row.port }}</span>
            </md-table-cell>
            <md-table-cell>
              <md-button class="md-icon-button">
                <md-icon @click.native="buttonEdit" :data-id="row._id">edit</md-icon>
              </md-button>

            </md-table-cell>
          </md-table-row>
        </md-table-body>
      </md-table>


      <tb-pagination
        :md-size="pageInfo.pageSize"
        :md-total="pageInfo.total"
        :md-page="pageInfo.pageNo"
        md-label=""
        md-separator="of"
        :md-page-options="[5, 10, 25, 50]"
        @pagination="getProject"
      ></tb-pagination>
    </md-table-card>

    <md-whiteframe md-elevation="10" v-if="showProj" class="app-project-show">
      <md-toolbar class="md-dense" md-theme="md-menu">
        <h1 class="md-title app-toolbar-title" style="flex:1;text-align: left;" v-html="proj.title"></h1>

        <md-button class="md-icon-button" @click="cancelVal('showProj')">
          
        </md-button>
      </md-toolbar>

      <div class="app-project-show-form">
         <md-input-container>
          <label></label>
          <md-input v-model="proj.model.name"></md-input>
        </md-input-container>

        <md-input-container>
          <label></label>
          <md-textarea v-model="proj.model.member"></md-textarea>
        </md-input-container>


        <md-input-container>
          <label></label>
          <md-input v-model="proj.model.path"></md-input>
        </md-input-container>


        <md-input-container>
          <label></label>
          <md-input v-model="proj.model.port"></md-input>
        </md-input-container>


        <md-input-container>
          <label>gulp</label>
          <md-textarea v-model="proj.model.gulp"></md-textarea>
        </md-input-container>


        <md-input-container>
          <label>webpack</label>
          <md-textarea v-model="proj.model.webpack"></md-textarea>
        </md-input-container>
      </div>
       
        <md-button class="md-primary" @click="submitProject"></md-button>     
    </md-whiteframe>


    <div class="app-input-search" v-show="showSearch">
      <md-input-container >
        <label></label>
        <md-input placeholder="" type="search" ></md-input>
      </md-input-container>
    </div>


    <md-dialog-alert
      :md-content="alertInfo.content"
      md-ok-text=""
      ref="alertInfo">
    </md-dialog-alert>

    <md-dialog-confirm
      :md-title="deleteInfo.title"
      :md-content-html="deleteInfo.contentHtml"
      :md-ok-text="''"
      :md-cancel-text="''"
      @close="deleteDiagClose"
      ref="deleteDiag">
    </md-dialog-confirm>
  </div>
</template>

<script>
  import tbPagination from '../components/tab-pagination.vue'

  export default {
    name: 'projectList',
    components: {
      tbPagination: tbPagination
    },
    data: function() {
      return {
        showSearch: false,
        showProj: false,
        list: [

        ],
        selectedItem: [],
        proj: {
          type: "add",
          title: "",
          model: {
            _id: '',
            name: '',
            member: '',
            path: '',
            port: '',
            gulp:'',
            webpack: '',
          }
        },
        deleteInfo: {
          title: '',
          contentHtml: ' ',
        },
        alertInfo: {
          content: ' ',
        },
        pageInfo: {
          total: 0,
          pageNo: 1,
          pageSize: 5,
        },
      };
    },
    created: function() {
      this.app = this.$resource('', {}, {
        get: {
          method: 'GET',
          url: '/mock/getAppProject'
        },
        add: {
          method: 'POST',
          url: '/mock/addAppProject'
        },
        edit: {
          method: 'PUT',
          url: '/mock/editAppProject'
        },
        delete: {
          method: 'DELETE',
          url: '/mock/deleteAppProject'
        },
        start: {
          method: 'PUT',
          url: '/mock/startAppProject'
        },
        stop: {
          method: 'PUT',
          url: '/mock/stopAppProject'
        },
      });
    },
    mounted: function() {
      this.getProject();
    },
    methods: {
      // 
      // 
      getProject: function(option) {
        if (option) {
          console.log(option);
          this.pageInfo.pageNo = option.page;
          this.pageInfo.pageSize = option.size;
        }
        var param = {
          pageSize: this.pageInfo.pageSize,
          pageNo: this.pageInfo.pageNo - 1
        };

        return this.app.get(param).then(function(res) {
          var data = res.data;
          if (data.code) {
            this.alert(data.err);
            return;
          }
          // 
          this.dealModelResult(data.data.list)
          this.list = data.data.list;
          this.pageInfo = {
            total: data.data.pagination.total,
            pageNo: data.data.pagination.pageNo + 1,
            pageSize: param.pageSize
          };
        });
      },
      // 
      addProject: function() {
        var param = this.copyObj({}, this.proj.model);

        var gulp = this.formatJSONString(model.gulp);
        var webpack = this.formatJSONString(model.webpack);

        if(!gulp){
          this.alert('gulp');
          return;
        }else if(!webpack){
          this.alert('webpack');
          return;
        }

        param.gulp = gulp;
        param.webpack = webpack;
        

        return this.app.add(param);
      },
      // 
      editProject: function() {
        var param = this.copyObj({}, this.proj.model);

        var gulp = this.formatJSONString(this.proj.model.gulp);
        var webpack = this.formatJSONString(this.proj.model.webpack);

        if(!gulp){
          this.alert('gulp');
          return;
        }else if(!webpack){
          this.alert('webpack');
          return;
        }

        param.gulp = gulp;
        param.webpack = webpack;
        param.id = param._id;
        return this.app.edit(param);
      },
      // 
      deleteProject: function(ids) {
        var param = {
          id: ids
        };
        return this.app.delete(param);
      },
      startProject: function(ids) {
        var param = {
          id: ids
        };
        return this.app.start(param);
      },
      stopProject: function(ids) {
        var param = {
          id: ids
        };
        return this.app.stop(param);
      },

      // 
      alert: function(msg) {
        this.$set(this.alertInfo, 'content', msg);
        // this.alertInfo.content = msg
        this.$refs['alertInfo'].open();
      },
      
      selectTable: function(e) {
        
      },
      // 
      buttonSearch: function(e) {
        this.showSearch = true;
      },
      closeDialog: function(ref) {
        this.$refs[ref].close();
      },
      // 
      openDiag: function(ref) {
        var inlineItems = this.getSelected(), itemArr = [];

        if (!inlineItems.length) {
          this.alert('');
          return;
        }

        for (var i = 0; i < inlineItems.length; i++) {
          itemArr.push('&nbsp;' + (i + 1) + '. ' + inlineItems[i].name);
        }

        this.deleteInfo.contentHtml = "<br/>" + itemArr.join('<br/>');
        this.$refs['deleteDiag'].open();
      },
      deleteDiagClose: function(e) {
        if (e === 'ok') {
          var ids = this.selectedItem.map(function(item) {
            return item._id
          });

          this.deleteProject(ids.join(',')).then(function(data) {
            data = data.data;
            this.getProject();
            this.alert(data.err || data.data.tip);
          }).catch(this.catchError);
        }
      },
      controlProjectStatus: function(e, type) {
        var inlineItems = this.getSelected();
        var ids = inlineItems.map(function(item) {
            return item._id
          });
        var req;
        if(type){
          req = this.startProject(ids.join(','))
        } else {
          req = this.stopProject(ids.join(','));
        }
        req.then(function(data) {
          data = data.data;
          this.getProject();
          this.alert(data.err || data.data.tip);
        }).catch(this.catchError);
        
      },
      // 
      dealProject: function(e, type) {
        if (type === 'add') {
          this.$set(this.proj, 'type', 'add');
          this.$set(this.proj, 'title', '');
          this.proj.model = {
            id: '',
            name: '',
            member: '',
            path: '',
            port: '',
            gulp: {
              path: '',
            },
            webpack: {
              path: '',
            },
          };

        } else if (type === 'edit') {
          this.$set(this.proj, 'type', 'edit');
          this.$set(this.proj, 'title', '');
        }
        this.showProj = true;
      },
      cancelVal: function(name) {
        this[name] = false;
      },
      // 
      submitProject: function(e) {
        var result;
        if (this.proj.type === 'add') {
          result = this.addProject();
        } else if (this.proj.type === 'edit') {
          result = this.editProject();
        }
        if(result)result.then(function(data) {
          data = data.data;
          if (!data.code) {
            this.showProj = false;
          }
          this.getProject();
          if (data.err) this.alert(data.err);
        }).catch(this.catchError);
      },
      // 
      buttonEdit: function(e) {
        var id = e.target.getAttribute('data-id');
        if (!id) return;
        let r = this.list.find(function(li) {
          return li._id === id;
        });
        if (!r) return;
        this.proj.model = r;
        this.dealProject(e, 'edit')
      },
      catchError: function(data) {
        this.alert('');
      },
      getSelected: function(){
        var query = document.querySelectorAll('#mytable table tbody tr.md-selected');
        var inlineItems = [];
        var list = this.list;
        query.forEach(function(item) {
          var _id = item.getAttribute('data-item') || '';
          var p = list.find(function(l) {
              return l._id === _id
            });
          if (p) {
            inlineItems.push(p)
          }
        });

        this.selectedItem = inlineItems;
        return inlineItems;
      },

      copyObj: function(to, from){
        for(var f in from){
          to[f] = typeof f === 'object'?
            this.copyObj(to[f] || {}, from[f]): from[f]
        }
        return to;
      },
      formatJSONString: function(str){
        str = str || '{}'
        var obj;
        try{
          obj = new Function('return ' + str + '')();
        }catch(e){
          return;
        }
        return JSON.stringify(obj)
      },
      prettyJSON: function(obj){
        return JSON.stringify(obj, null, 4)
      },
      dealModelResult: function(models){
        if(Object.prototype.toString.call(models) !== '[object Array]'){
          models = [models]
        }
        models.forEach((model)=>{
          model.gulp = this.prettyJSON(model.gulp)
          model.webpack = this.prettyJSON(model.webpack)
        });
      }
    }
  }
</script>

<style>
  .app-toolbar {}
  
  .app-input-search {
    width: 40%;
    position: fixed;
    top: 64px;
    left: 30%;
    z-index: 1;
    background-color: white;
    padding: 0 30px;
    box-shadow: 0 3px 5px #666666;
    border-radius: 3px;
  }
  
  .app-project-show {
    margin: 20px auto;
    text-align: center;
    max-width: 1000px;
  }
  
  .app-project-show-form {
    padding: 0 40px;
    
  }
  .m-project-card {
    max-width: 1200px;
    margin: 0 auto;
  }
  .m-inline {
    margin: 0;
  }
  .md-table .md-table-cell.md-has-action .md-table-cell-container {
    justify-content: flex-start;
  }
  .m-min-width {
    width: 60px;
  }
</style>


